.header-container .MuiAppBar-root {
  background: linear-gradient(90deg, #c3a2fe, #9358fd);
  box-shadow: none;
}
.header-container .header-link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  margin: 10px;
  color: rgb(0, 0, 0);
  font-family: "Harrington";
  font-weight: bold;
}
.header-container .header-link:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.header-container .header-link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.header-container .header-link.active {
  color: rgb(255, 255, 255);
  font-weight: bold;
}
.header-container .header-link.active:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: rgb(255, 255, 255);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.header-container .header-link.active:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.header-container .name-symbol {
  color: rgb(255, 255, 255);
  cursor: pointer;
  font-family: "Mangal";
  margin-left: 5%;
  border: 2px solid #fff;
  padding: 0px 8px;
  font-weight: bold;
}
.header-container .name-symbol:hover {
  animation: flick 2s 1;
}
.header-container .nav-item-container {
  margin: auto;
}
.header-container .sidebar-toggler-button {
  color: rgb(255, 255, 255);
}
.header-container .profile_image {
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 100%;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 5%;
}
.header-container .profile_image img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.drawer-container .MuiPaper-root {
